* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');


h1,
h2,
h3,
h4,
h5 {
    line-height: 1;
    font-family: "Montserrat", sans-serif;
    font-optical-sizing: auto;
    font-weight: 700;
    font-style: normal;
}

.hero-container {
    display: grid;
    min-height: 100vh;
    grid-template-rows: auto 1fr auto;
    padding: 10px 5%;
    /* Ajusté el padding para hacerlo más flexible */
    max-width: 100%;
    /* Agregué max-width para limitar el ancho máximo */
    overflow: hidden;
    background-image: linear-gradient(rgba(62, 62, 131, 0.5), rgba(255, 255, 0, 0.5)),
        url("/public/imgs/cancha_banner.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

}

.nav-hero-container {
    width: 100%;
    padding: 5px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}


/* Diseño para logo*/
/* contenedor de la imagen */

.logo-container {
    width: 50%;
    max-height: max-content;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 5px;

}

.logo-container .logo-img {
    width: 350px;
    padding: 5px;    
    filter: drop-shadow(0px 0 1px rgba(0, 0, 0, 0.8));
}

.logo-slogan {
    display: none;
}

