/*botones*/
.h-button-container {
    width: 70%;
    display: flex;
    padding: 15px;
    align-items: center;
    justify-content: flex-end;
    flex-wrap: wrap-reverse;
    gap: 20px;
}

.h-button-container a {
    /* Espacio entre los elementos */
    width: 200px;
    padding: 10px;
    border-radius: 5px;
    text-decoration: none;
    font-weight: 700;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    text-align: center;
    color: #252525;
}

.pelotita {
    font-size: 3rem;
}

.h-button-container a:nth-child(1) {
    background-color: #00b04b;
    padding: 12px;
    color: white;
}

.h-button-container a:nth-child(2) {
    color: azure;
    background-color: #25252528;
    border: 1.2px solid #ffff;
}
.h-button-container a:nth-child(2):hover{
    background-color: #00b04b;
    transition: all 1.1s ease-in;
}





/*Estilos para la descripcion*/
.h-description-container {
    width: 100%;
    max-width: 100%;
    /* Agregué max-width para limitar el ancho máximo */

    padding: 18px;
    margin-bottom: 50px;
}

.h-title {
    font-size: 5em;
    line-height: 1;
    font-family: "Montserrat", sans-serif;
    font-optical-sizing: auto;
    font-weight: 700;
    font-style: normal;
    color: #fcfcfc;

}

.h-text {
    margin-top: 20px;
    font-size: 1.5rem;
    line-height: 2rem;
    text-align: left;
    text-wrap: wrap;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}


.b-container{
    display: flex;
    align-items: center;
    justify-content: center;
}
.buscador-container {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 80vw;
    margin-top: 20px;
    border-radius: 39px;
    border: 0.22px solid #2c292939;
    background-color: #aea6a613;
    box-shadow: 10px 10px 55px #3e7255b0;
    margin-bottom: 50px;
}

.b-list-container {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5px;
    max-width: 90%;
    padding: 15px;
    border-radius: 26px;
    border-radius: 50px;
}

.b-list-container>* {
    width: 100%;
    text-align: center;
    padding: 9px;
}

.h-1{
    order:1;
}
.h-2{
    order:2;
}
.h-3{
    order:3;
}


.buscador-container input {
    width: 1/4vw;
    height: 35px;
    text-align: center;
    border: 1px solid #e5e5e5;
    border-radius: 16px;
    cursor: pointer;
}


@media (width <=750px) {
    .hero-container{
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    padding: 10px 5%;
    max-width: 100%;
    overflow: hidden;
    /* background-image: linear-gradient(rgba(255, 0, 0, 0.5), rgba(255, 255, 0, 0.5)), url(http://localhost:3000/static/media/cancha_banner.63978bb….jpg); */
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    }
    .h-1{
        order:1;
        height: 180px;
    }
    .h-2{
        order:3;
    }
    .h-3{
        order:2;
    }
    .h-title {
        font-size: 3.2rem;
    
    }
    .h-button-container {
        display: none;
    }
    /* LOGO Y TEXTO */
    .nav-hero-container .logo-container .logo-img{
        width: 50px;
    }
    /* BUSCADOR */

    .buscador-container {
        max-width:90% ;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 10px;
        border-radius: 50px;
        margin-bottom: 50px;

    }
    .b-list-container{
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: auto auto auto auto; /* 4 filas en total */
        gap: 10px; /* Espacio entre los elementos */
    }
    .b-list-container > *{
        min-width: 90%;
    }
    .b-1{
        grid-column: 1/-1;/*ocupa ambas columnas*/
        grid-row: 1; /*1 era fila*/
    }
    .b-2{
        grid-column: 1/-1;/*ocupa ambas columnas*/
        grid-row: 2; /*1 era fila*/
    }
    .b-3{
        grid-column: 1;/*ocupa ambas columnas*/
        grid-row: 3; /*1 era fila*/
    }
    .b-4{
        grid-column: 2;/*ocupa ambas columnas*/
        grid-row: 3; /*1 era fila*/
    }
    .b-5{
        
        grid-column: 1/-1;/*ocupa ambas columnas*/
        grid-row: 4; /*1 era fila*/
    }
    


    /* DESCRIPCION */
.h-description-container{
    padding: 5px;
}
.h-text{
    margin-top: 2px;
}

}



.buscador-container input {
    font-size: 0.9rem;
    color: rgb(3, 3, 3);
}

.buscador-container input:nth-child(5) {
    background-color: #00af4a;
}


/*PARTE MAIN DE LA INFO*/

.m-que-hacemos {
    width: 90%;
    min-height: 70dvh;
    margin-left: 5%;
    margin-top: 50px;
    padding: 28px;
    gap: 50px;
    display: flex;
    justify-content: center;

}

.m-qh-info {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.m-qh-btn {
    width: 50%;
    padding: 10px;
    margin-top: 40px;
}

.m-qh-info h2 {
    color: #00b04b;
    font-size: 3.6rem;
}

.m-qh-info p {
    margin-top: 20px;
    font-size: 1.5rem;
    line-height: 2rem;
    text-align: left;
    text-wrap: wrap;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.m-qh-img {
    display: flex;
    align-items: center;
    justify-items: center;
    height: auto;
    clip-path: circle(60% at 50% 50%);
}


.lista-beneficios{
    display: flex;
    flex-direction: column;
    padding: 20px;
    text-align: left;
    font-weight: 600;
    font-size: 1.5rem;
}

.m-nuestros-clientes {
    background-color: #00b04b;
    padding: 30px;
    margin-top: 100px;
}

.m-nc-title {
    color: #ffffff;
    text-align: center;
    font-size: 2.5rem;
}

.m-img-container {
    width: 100%;
    margin-top: 30px;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: space-around;


}

.img-nuestros-clientes {
    width: 150px;
    filter: grayscale();
    transition: all 0.5s ease;
}

.img-nuestros-clientes:hover {
    filter: grayscale(0);
    transition: all 0.5s ease;
    transform: scale(1.2);
}


/* TARJETAS */
.cards-container{
    display: flex;
    flex-wrap: wrap;
    gap: 30px;
    justify-content: center;
    
}
