.App {
  text-align: center;
}
body{
  height: 100vh;
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

/* estilos para el banner y su contenedor */
header{
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: aliceblue;
}
.img-container{
  width: 95vw;
  height: 400px;
}
.banner-imgs{
  height: 500px;
  width: 95%;
  max-height: 95%;
  border-radius: 10px;
  box-shadow: 5px 1px 7px 1px rgba(0, 0, 0, 0.5);

}

.titulo-banner{
  font-weight: 700;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 3.5rem;
  background-color: aliceblue;
  width: 100%;
  height: 100px;
}

/* ESTILOS PARA LA FECHA Y HORA */
.fyh-container{
  background-color: aliceblue;
  padding: 10px;
}
.fyh-container > *{
  font-size: 1.5rem;
}

/* REDES SOCIALES */
footer{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: #00b04b;
  padding: 35px;
}
footer>*{
  color: white;
  margin: 5px;
}
footer>h4{
  color: aliceblue;
}
.redes-container{
  display: flex;
  gap: 8px;
}
.redes-img{
  width: 40px;
  cursor: pointer;
  margin: 10px;
}

/*FORMULARIO */

form{
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  align-items: center;
  gap: 15px;
  padding: 25px;
  margin-bottom: 50px;
  margin-top: 20px;
}

form > *{
  width: 50%;
  
}
form > h2{
  color: rgb(8, 9, 10);
  font-weight: 700;
  font-size: 1.9rem;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}


.form-group{
  display: flex;
  flex-direction: column;
  gap: 5px;
  color: rgb(0, 0, 0);
  font-weight: 600;
}
.form-ctrl{
  padding: 9px;
}
.tyc{
  cursor: pointer;
  
}

.btn{
  max-width: 500px;
  padding: 10px;
  font-weight:700;
}

.f-container{
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(255, 255, 255);
}

.i-color-form{
  background: linear-gradient(90deg, #00F260, #0575E6);
  -webkit-background-clip: text;
  color: transparent;
  font-size: 1rem; /* Ajusta el tamaño según tus necesidades */
  font-weight: bold;
  font-family: 'Roboto', sans-serif; /* O cualquier otra fuente futurista */
}

/* ESTILOS PARA EL CARD QUE APARECE LUEGO QUE TE REGISTRAS */
.registrado-default{
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: column;
  gap: 10px;
  padding: 10px;
  width: 350px;
  height: 350px;
  background-color: rgb(57, 151, 233);
  border-radius: 26px;
  transition: all 0.5s ease-in-out;
  box-shadow: 3px 0px 16px 0.5px rgba(0, 0, 0, 0.5);

}
.registrado-default:hover{
  background-color: rgba(57, 151, 233, 0.858);
  transition: all 0.5s ease-in-out;
}


.r-d-text{
  font-size: 1.8rem;
  color: white;
  font-weight: 600;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.btn-warning{
  font-weight: 600;
}
.btn-warning:hover{
 background-color: white;
}


